class subtitleParser {
    constructor(){
        this.rawText = null;
        this.srtGroup = null;

        this.keywords = null;
    }

    parseFromString(inputStr){
        this.rawText = inputStr;
    }

    parseFromUrl(url, onProgress){
        return new Promise((resolve, reject) => {
            const xhr = new XMLHttpRequest();
            xhr.onload = () => {
                this.rawText = xhr.responseText;

                return resolve();
            };

            xhr.onerror = () => {
                return reject();
            }

            xhr.onProgress = () => {
                if(typeof onProgress == 'function'){
                    onProgress(xhr.loaded, xhr.loaded);
                }
            }

            xhr.open('GET', url);
            xhr.send();
        });
    }

    //json file
    loadKeyword(url){
        return new Promise((resolve, reject) => {
            const xhr = new XMLHttpRequest();
            xhr.onload = () => {
                this.keywords = JSON.parse(xhr.responseText);

                return resolve();
            };

            xhr.onerror = () => {
                return reject();
            }

            xhr.open('GET', url);
            xhr.send();
        });
    }

    parseText(){
        

        if(this.rawText === null){
            console.error('No input Text');
            return;
        }

        const srtSplit = this.rawText.split(/[\n\r]/).filter(e => e.length);

        const order = [];

        for(let i  = 0 ; i < srtSplit.length; i++){
            if(/^[0-9]+$/.test(srtSplit[i])){
                order.push(i);
            }
        }

        const srtGroup = [];

        const getTimeStamp = durationArr => {
            const d = new Date();
            d.setTime(0);

            d.setHours(8 + durationArr[0]);
            d.setMinutes(durationArr[1]);
            d.setSeconds(durationArr[2]);
            d.setMilliseconds(durationArr[3]);

            return d.getTime();
        }

        for(let i = 0 ; i<order.length; i++){
            const duration = srtSplit[order[i] + 1].split(/\s+\-\-\>\s+/);

            let tmpDurationArr = duration[0].split(/[\:\,\‚]/).map(e => +e);
            const start = getTimeStamp(tmpDurationArr);

            tmpDurationArr = duration[1].split(/[\:\,\‚]/).map(e => +e);
            
            const end = getTimeStamp(tmpDurationArr);



            const tmpGroup = {
                timeline: null,
                subtitle: [],
                id: i
            };

            tmpGroup.timeline = [start, end];
        

            for(let j = order[i] + 2; (i+1) >= order.length ? j< srtSplit.length : j < order[i+1]; j++){
                tmpGroup.subtitle.push(srtSplit[j]);
            }

            tmpGroup.subtitle = tmpGroup.subtitle.filter(e => e.length);
            srtGroup.push(tmpGroup);
        }

        this.srtGroup = srtGroup;
    }

    update(currentTimeStamp){
        if(this.srtGroup === null){
            return console.error("No subtitle group!");
        }

        for(let i of this.srtGroup){
            if(currentTimeStamp >= i.timeline[0] && currentTimeStamp <= i.timeline[1]){
                return { 
                    currentSubtitle: i.subtitle,
                    id: i.id,
                    start: i.timeline[0],
                    end: i.timeline[1]
                };
            }
        }

        return false;
    }
}

export default subtitleParser;