<script>
	import Layout from './Components/Layout.svelte';

	import { resizer } from './store/windowResize';


	const handleResize = () => {
		const { setSize } = resizer;
		setSize(window.innerWidth, window.innerWidth);
	}


</script>

<main id="App">
	<Layout />
</main>

<svelte:window on:resize={handleResize} />

<style>
	#App {
		height: 100%;
		width: 100%;
	}
</style>