import * as d3 from 'd3';

const effect = args => {
    const { fontViewWidth, fontColor, subtitleStr, svg, containerWidth, containerHeight, duration, keywords } = args;

    const tempText = svg.datum(subtitleStr)
        .append('text')
        .attr('x', containerWidth / 2)
        .attr('y', containerHeight / 2)
        .attr('text-anchor', 'middle')
        .attr('font-family', `'Nova Mono', monospace`)
        .attr('font-size', `${fontViewWidth}vw`)
        .text(d => d)
        .node()
    ;

    const bounding = tempText.getBoundingClientRect();

    tempText.remove();
    const offset = (containerWidth - bounding.width) / 2;

    let tmpInputData = subtitleStr.split(' ');

    tmpInputData = tmpInputData.map((e,i) => { 
        if(i == tmpInputData.length - 1){
            return { text: e, percent: (e.length) / subtitleStr.length}
        }
        return { text: e, percent: (e.length+1) / subtitleStr.length}
    });

    const inputData = tmpInputData.map((e, i) => {
        let accumulate = 0;

        if(i!= 0){
            for(let k = 0; k<=i-1; k++){
                accumulate += tmpInputData[k].percent
            }
        }

        return { text: e.text, accumulate }
    });

    const texts = svg.selectAll('text')
        .data(inputData)
        .enter()
        .append('text')
        .attr('x', (d, i) => i == 0 ? offset : d.accumulate * bounding.width + offset)
        .attr('y', containerHeight / 2 + bounding.height/3)
        .attr('text-anchor', 'left')
        .attr('font-size', `${fontViewWidth}vw`)
        .attr('font-family', `'Nova Mono', monospace`)
        .attr('fill', fontColor)
        .style('opacity', '0')
        .text(d => d.text)
    ;

    let isRotated = 1;
    texts.each(function(d,i){

        const transition = d3.select(this).transition();

        transition.duration(duration).delay(_ => i*duration);
    

        const findKeywords = keywords.find(e => e.text == d.text);


        if(typeof findKeywords != 'undefined'){
            for(let i of findKeywords.effects){
                const [ effect, value ] = i.split(',');

                transition.style('opacity', '1');

                
                switch(effect){
                    case 'scale':
                        transition
                            .style('font-size', `${fontViewWidth * +value}vw`)
                            .style('transform', `translate(${-fontViewWidth/2}vw, ${fontViewWidth * isRotated/3}vw) rotate(-1.5deg)`)
                        ;
                        isRotated++;
                        break;
                    case 'color':
                        transition.attrTween("fill", () => d3.interpolateRgb(fontColor, value));
                        break;
                    case 'weight':
                        transition.attr('font-weight', value);
                        break;
                    case 'case':
                        d3.select(this).text(d.text.toUpperCase());
                        break;
                    case 'decoration':
                        d3.select(this).attr('text-decoration', value);
                        break;
                    case 'rotate':
                        d3.select(this)
                            .datum(d.text)
                            .append('animate')
                            .attr('attributeName', "rotate")
                            .attr('dur', duration / findKeywords.effects.length )
                            .attr('from', d => new Array(d.length).fill(0))
                            .attr('to', d => new Array(d.length).fill(value))
                            .attr('begin', -999999)
                        ;
                }
            }
        }

        transition.style('opacity', '0.8');
    });
};




export default effect;

