<script>
    import { onMount } from 'svelte';
    import * as d3 from 'd3';


    import { videoDuration } from '../store/videoDuration';

    import SubtitleEffect from '../Utilities/subtitleEffect';

    export let containerVerticalBounding;
    export let parsedSubtitle;

    let subtitleElement = null;


    const handleDragStart = evt => {
        console.log('User start to drag subtitle container');
    }
    const handleDragEnd = evt => {
        const { pageY } = evt;
        const { top, bottom } = containerVerticalBounding;

        if(pageY < top || pageY > bottom - subtitleElement.clientHeight){
            return;
        }
        subtitleElement.style.top = pageY + 'px';
    }


    let prevSubtitleId = -1;
    const unSubscribeVideoDuration = videoDuration.subscribe(duration => {

        if(subtitleElement === null){
            return;
        }

        if(duration == -1){
            return;
        }  

        const subtitleRst = parsedSubtitle.update(duration * 1000);

        if(subtitleRst === false){
            return;
        }

        const { currentSubtitle, id, start, end  } = subtitleRst;

        if(prevSubtitleId == id){
            return;
        }

        prevSubtitleId = id;

        d3.select(subtitleElement)
            .select('svg')
            .remove()
        ;

        const svg = d3.select(subtitleElement)
                        .append('svg')
                        .attr('width', subtitleElement.clientWidth)
                        .attr('height', subtitleElement.clientHeight)
        ;

        const subtitleString = currentSubtitle.join(' ');

        SubtitleEffect({
            fontViewWidth: 2,
            fontColor: '#b3f090',
            subtitleStr: subtitleString,
            svg,
            containerWidth: subtitleElement.clientWidth,
            containerHeight:  subtitleElement.clientHeight,
            duration: 200,
            keywords: parsedSubtitle.keywords
        });
    });

    onMount(() => {


        return () => {
            unSubscribeVideoDuration();
        }
    });
    
</script>


<div 
    id="Subtitle"
    draggable=true
    on:dragstart={handleDragStart}
    on:dragend={handleDragEnd}
    on:drag={handleDragEnd}
    bind:this={subtitleElement}
>
    
</div>

<style>
    #Subtitle {
        position: absolute;
        z-index: 999;
        cursor: move;
        height: 10%;
        width: 100%;
        font-weight: bold;
        color: white;

        user-select: none;
        bottom: 15%;
        left: 0;

        display: flex;
        justify-content: center;
        align-items: center;
        border-top-style: solid;
        border-bottom-style: solid;
        border-width: .1px;
    }
</style>