<script>
    import { onMount } from 'svelte';

    import { videoDuration } from '../store/videoDuration';
    import { resizer } from '../store/windowResize';
    import { progress } from '../store/progress';
    import { videoInfo } from '../store/videoInfo';

    import subtitleParser from '../Utilities/subtitleParser';

    import Subtitle from '../Components/Subtitle.svelte';

    let videoElement;
    let videoContainer;

    let videoVerticalBounding = null;
    let parsedSubtitle = null;


    export let videoSrc;
    export let subtitleSrc;

    onMount(() => {

        const waitVideo = new Promise((resolve) => {
            videoElement.addEventListener('canplaythrough', () => {
                resolve();
            })
        });

        const unSubscribeResizer = resizer.subscribe(() => {
            videoElement.width = videoContainer.clientWidth;
            videoElement.height = videoContainer.clientHeight;

            videoVerticalBounding = {
                top: videoContainer.getBoundingClientRect().y,
                bottom: videoContainer.getBoundingClientRect().y + videoContainer.getBoundingClientRect().height
            }
        });

        let stop = false;

        const initalize = async () => {
            progress.setProgress({
                finish: false,
                type: 'video',
                value: 0
            });

            await waitVideo;

            progress.setProgress({
                finish: false,
                type: 'video',
                value: 100
            });

            videoElement.width = videoContainer.clientWidth;
            videoElement.height = videoContainer.clientHeight;

            

            const sp = new subtitleParser();

            await sp.parseFromUrl(subtitleSrc, (loaded, total) => {
                progress.setProgress({
                    finish: false,
                    type: 'subtitle',
                    value: loaded / total
                });
            });

            progress.setProgress({finish: true});

            await sp.loadKeyword('./media/keywords.json');

            sp.parseText();

            videoElement.style.display = "block";

            // re-render
            videoVerticalBounding = {
                top: videoContainer.getBoundingClientRect().y,
                bottom: videoContainer.getBoundingClientRect().y + videoContainer.getBoundingClientRect().height
            };

            parsedSubtitle = sp;

            videoInfo.setInfo({
                name: videoSrc.split('/').slice(-1)[0],
                subtitle: subtitleSrc.split('/').slice(-1)[0],
                duration: videoElement.duration
            });


            const getVideoCurrentTime = () => {
                if(stop){
                    return;
                }
                window.requestAnimationFrame(getVideoCurrentTime);
                videoDuration.setDuration(videoElement.currentTime);
            }

            getVideoCurrentTime();
        }

        
        initalize();
        

        return () => {
            stop = true;
            unSubscribeResizer();
        }
    });
</script>


<div id="videoContainer" bind:this={videoContainer}>
    <video 
        bind:this={videoElement}
        controls
    >
        <source src="{videoSrc}" type="video/mp4" />
    </video>

    {#if parsedSubtitle !== null}
        <Subtitle 
            containerVerticalBounding={videoVerticalBounding}
            parsedSubtitle={parsedSubtitle}
        />
    {/if}
</div>

<style>
    #videoContainer {
        width: 70%;
        height: 100%;
        position: relative;
        overflow: hidden;

        animation: show-in 1s ease;
    }

    video {
        object-fit: cover;
        position: absolute;
        display: none;
    }

    video::-webkit-media-controls-fullscreen-button {
        display: none;
    }

    @keyframes show-in {
        from {
            opacity: 0;
            transform: translateX(999px);
        }

        to {
            opacity: 1;
            transform: translateX(0px);
        }
    }
</style>