<script>
    import { onMount } from 'svelte';

    import ControlSide from '../Components/ControlSide.svelte';
    import VideoLoader from '../Components/VideoLoader.svelte';

    let layout;

    onMount(() => {
        setTimeout(() => {
            layout.style.overflow = 'auto';
        }, 1200);
    })
</script>



<div id="Layout" bind:this={layout}>
    <ControlSide />

    <VideoLoader 
        videoSrc={'./media/still_dre.mp4'}
        subtitleSrc={'./media/still_dre.srt'}
    />
</div>

<style>
    #Layout {
        height: 100%;
        width: 100%;

        display: flex;
        flex-direction: row;
        overflow: hidden;
}
</style>