<script>
    import * as d3 from 'd3';
    import { onMount } from 'svelte';

    import { progress } from '../store/progress';

    let progressOuter;
    let progressContainer;
    
    const unSubscribeProgress = progress.subscribe(val => {
        if(typeof progressOuter == 'undefined'){
            return;
        }

        const { value, type } = val;

        progressOuter.style.width = value + '%';

    });


    onMount(() => {
        const svg = d3.select(progressOuter)
                        .append('svg')
                        .attr('width', progressContainer.clientWidth)
                        .attr('height', progressContainer.clientHeight)
        ;

        const rect = svg.append('rect')
                            .attr('x', 0)
                            .attr('y', 0)
                            .attr('width', progressContainer.clientWidth)
                            .attr('height', progressContainer.clientHeight)
                            .attr('fill', '#80ccff')
        ;

        return () => {
            unSubscribeProgress();
        }
    });

</script>

<div id="progress-container" bind:this={progressContainer}>
    <div id="progress-outer" bind:this={progressOuter}>
    </div>
</div>

<style>
    #progress-container {
        width: 100%;
        height: 5%;
    }

    #progress-outer {
        width: 0%;
        height: 100%;
        overflow: hidden;
    }

</style>