<script>
    import { onDestroy } from 'svelte';
    
    import { progress } from '../store/progress';
    import { videoInfo } from '../store/videoInfo';

    import ProgressBar from '../Components/ProgressBar.svelte';


    let finish;
    const unSubscribeProgress = progress.subscribe(val => {
        finish = val.finish;
    });

    let videoInfoResult = null;
    const unSubscribeVideoInfo = videoInfo.subscribe(val => {
        videoInfoResult = val;
    })


    onDestroy(() => {
        unSubscribeProgress();
        unSubscribeVideoInfo();
    });

</script>


<div id="ControlBar">
    <div class="content">
        <h1 style="font-family: 'Julee', cursive;"> Lab09 SVG Subtitle </h1>
    </div>

    <div >
        {#if videoInfoResult !== null}
            <div class="inner"> 影片名: <span class="inner-content"> { videoInfoResult.name } </span> </div>
            <div class="inner"> 字幕名: <span class="inner-content"> { videoInfoResult.subtitle } </span> </div>
            <div class="inner"> 影片時長: <span class="inner-content"> { `${Math.floor(videoInfoResult.duration / 60)}:${Math.floor(videoInfoResult.duration % 60)}` } </span> </div>
            <div class="inner"> 字幕字型: 
                <span class="inner-content"> 
                    <a href="//fonts.google.com/specimen/Nova+Mono" rel="noopener" target="_black" style="text-decoration: none">@Google</a> 
                    Nova Mono 
                </span> 
            </div>
        {/if}
    </div>

    {#if !finish}
        <ProgressBar />
    {:else}
        <div class="content progress-bar">
            <span style="font-family: Julee"> Powered By </span>
            <span style="font-family: 'Charmonman', cursive"> D3.js </span>
        </div>
    {/if}

</div>

<style>
    #ControlBar {
        height: 100%;
        width: 30%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        animation: show-in 1s ease;
    }

    .content {
        width: 100%;
        text-align: center;
    }

    .inner-content {
        color: rgb(106, 106, 177);
    }

    .inner {
        margin: .3em auto;
    }

    @keyframes show-in {
        from {
            opacity: 0;
            transform: translateX(-999px);
        }

        to {
            opacity: 1;
            transform: translateX(0px);
        }

    }
</style>